/*
 *  main.css
 *
 *  The main.css file is for site-wide styling. 
 *
 *  This file overrides many of the styles for default HTML tags and defines
 *  many color variables in the root context.
 *
 * * * */

:root {
  --main-bg: #ebe6e1;
  --main-fg: #362c22;
}

/*
 *
 * * * */
body {
  background: var(--main-bg);
  color: var(--main-fg);
  margin: 0 auto;
  max-width: 800px;
}


/*
 *
 * * * */
h1 {
  font-family: "IM Fell English", serif;
  text-transform: uppercase;
}
